<template>
    <div class="tag-list">
        <el-table
                ref="changeTable"
                v-show="!tagId"
                v-loading="loading"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                border
                :data="dataList"
                @select="handleSelectOrCancel"
                @select-all="handleSelectAllOrCancel"
        >
            <el-table-column
                    type="selection"
                    width="55"
                    v-if="showSelection"
            >
            </el-table-column>
            <el-table-column prop="tagId" label="ID">
            </el-table-column>
            <el-table-column prop="tagName" label="">
                <template slot-scope="scope">
                    {{scope.row.tagName}}
                    <span v-if="scope.row.newOrUpdate" class="red-new">{{scope.row.newOrUpdate}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="dataSource" label="" >
                <template slot-scope="scope">
                    {{ dataSourceEnum[scope.row.dataSource] }}
                </template>
            </el-table-column>
            <el-table-column
                    v-if="(checkList.indexOf('defineRemark') > -1)"
                    prop="defineRemark"
                    label=""
            >
            </el-table-column>
            <el-table-column
                    v-if="(checkList.indexOf('tagValues') > -1)"
                    prop="tagValues"
                    label=""
            >
            </el-table-column>
            <el-table-column
                    v-if="(checkList.indexOf('tagKey') > -1)"
                    prop="tagKey"
                    label="code"
            >
            </el-table-column>
            <el-table-column
                    v-if="(checkList.indexOf('tagType') > -1)"
                    prop="tagType"
                    label=""
            >
                <template slot-scope="scope">
                    {{ typeEnum[scope.row.tagType] }}
                </template>
            </el-table-column>
            <el-table-column
                    v-if="(checkList.indexOf('remark') > -1)"
                    prop="remark"
                    label=""
            >
            </el-table-column>
            <el-table-column
                    prop="operation"
                    label=""
                    v-if="!showSelection"
            >
                <!-- slot-scope="{ column, $index }" -->
                <template
                        slot="header"
                >
                    
                    <el-popover
                            placement="top"
                            trigger="click"
                            class="popover-button"
                    >
                        <div>
                            <el-checkbox-group v-model="checkList" @change="handleCheckListChange">
                                <el-checkbox label="tagKey">code</el-checkbox>
                                <el-checkbox label="tagValues"></el-checkbox>
                                <el-checkbox label="defineRemark"></el-checkbox>
                                <el-checkbox label="tagType"></el-checkbox>
                                <el-checkbox label="remark"></el-checkbox>
                            </el-checkbox-group>
                        </div>
                        <i
                                class="el-icon-cc-setting operate"
                                slot="reference"
                        >
                        </i>
                    </el-popover>
                </template>
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button
                                type="text"
                                @click="handleSeeTagCategoryDetail(scope.row)"
                        >
                            
                        </el-button>
                        <div
                                :class="scope.row.myCollect ? 'el-icon-cc-star-fill': 'el-icon-cc-star'"
                                @click="handleCollect(scope.row)"
                        >
                        </div>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <tag-detail-list
                v-if="tagId"
                :tag-id="tagId"
                @read-cancel="handleReadTagCancel"
        >
        </tag-detail-list>
    </div>
</template>

<script>
    import tagDetailList from './TagDetail'
    export default {
        name: "TagList",
        props: {
            dataList: {
                type: Array
            },
            dataSourceEnum: {
                type: Object
            },
            typeEnum: {
                type: Object
            },
            checkListParent: {
                type: Array
            },
            loading: {
                type: Boolean
            },
            showSelection: {
                type: Boolean
            },
            currentSelectedTags: {
                type: Array
            }
        },
        components: {
            tagDetailList
        },
        data () {
            return {
                multipleSelection: [],
                tagId: undefined,
                checkList: []
            }
        },
        watch: {
            checkListParent: function (val) {
                this.checkList = val
            },
            'dataList': 'updateTableSelected',
            'currentSelectedTags': 'updateTableSelected'
        },
        methods: {
            handleCheckListChange (val) {
                this.$emit('change-checkList',val)
            },
            handleSeeTagCategoryDetail (row) {
                this.tagId = row.tagId
            },
            handleReadTagCancel () {
                this.tagId = undefined
            },
            handleCollect (currentTag) {
                const flag = currentTag.myCollect
                const tagId = currentTag.tagId
                if (flag) {
                    //    
                    this.$service.cancelCollectTags({tagId},'').then(() => {
                        this.$emit('fetch-data')
                    })
                } else {
                    //    
                    this.$service.collectTags({tagId},'').then(() => {
                        this.$emit('fetch-data')
                    })
                }
            },
            updateTableSelected () {
                const arr = []
                const currentSelectRows = this.currentSelectedTags
                this.dataList.forEach((item, index) => {
                    currentSelectRows.forEach((i) => {
                        if (item.tagId === i.tagId) {
                            arr.push(this.dataList[index])
                        }
                    })
                })
                if (arr.length > 0) {
                    // 
                    this.$nextTick(() => {
                        this.$refs.changeTable.clearSelection()
                        arr.forEach(row => {
                            this.$refs.changeTable.toggleRowSelection(row,true)
                        })
                    })
                } else {
                    this.$refs.changeTable.clearSelection()
                }
            },
            handleSelectOrCancel (select, row) {
                const selectedFlag = select.length && select.indexOf(row) !== -1
                // true0false
                if (selectedFlag) {
                    this.$refs.changeTable.toggleRowSelection(row,true)
                    this.$emit('table-selected',row, 'add')
                } else {
                    this.$refs.changeTable.toggleRowSelection(row,false)
                    this.$emit('table-selected',row, 'del')
                }
            },
            handleSelectAllOrCancel (select) {
                // select0
                const data = this.dataList
                if (select.length === 0) {
                    for (var i=0;i<data.length;i++) {
                        this.$emit('table-selected',data[i], 'del')
                    }
                } else {
                    for (var j=0;j<data.length;j++) {
                        this.$emit('table-selected',data[j], 'add')
                    }
                }
            }
        },
        created () {
            this.checkList = this.checkListParent
        }
    }
</script>

<style lang="stylus" scoped>
    .tag-list >>> .el-icon-cc-star-fill
        color #E6A13C
    .tag-list >>> .el-button-group
        display flex
        align-items center
        .el-button
            margin 0 5px
    .operate
        margin-left 20px
        cursor pointer
    .red-new
        color #ff0000
        position absolute
        top 0
</style>
